@import "./variables";

.primary-bg {
    background: $primary-color;
    color: $white;
}

.secondary-bg {
    background: $secondary-color;
    color: $black;
}

.primary-color {
    color: $white;
}

.secondary-color {
    color: $black;
}

.primary-border {
    border-color: $primary-color;
    color: $secondary-color;
}

.secondary-border {
    border-color: $secondary-color;
    color: $primary-color;
}

.active-color {
    background-color: $active-color;
}

.page {
    padding: 30px 0; 
    height: calc(100vh - 125px);
    overflow: auto;   
}

.heading {
    margin-bottom: 60px;
}
.heading h2 {
    position: relative;
}
.heading h2:before {
    content: '';
    position: absolute;
    height: 4px;
    background-color: $active-color;
    width: 100px;
    bottom: -12px;
}